.link-card {
  padding: 1em;
  background-color: rgba(55, 55, 55, 0.8);
  width: 220px;
  min-height: 300px;
  margin: 0.7em;
  box-shadow: 2px 3px rgba(0, 0, 0, 0.5);
}

h3 {
  margin-top: 0;
  font-weight: 600;
  text-decoration: underline;
  display: inline;
}

li {
  margin: 5px 0;
  font-size: 17px;
}

a {
  color: #e9e9e9;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.tag {
  font-size: 12px;
  margin-left: 1em;
  background-color: #e9e9e9;
  color: black;
  padding: 0.2em;
  padding-right: 0.3em;
  border-radius: 5px;
}

.senac {
  color: white;
  background-color: rgba(60, 60, 173, 0.8);
}

.estudos {
  color: white;
  background-color: rgba(66, 146, 66, 0.856);
}

.curly {
  display: inline;
  color: #ec6e4c;
  font-size: 20px;
  font-weight: 600;
}

ul {
  margin-bottom: 0;
}
